<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li
        class="page-item"
        :class="{ disabled: pages.currentPage === 1 || pages.pageTotal === 0 }"
      >
        <a
          class="page-link"
          href="#"
          aria-label="Previous"
          @click.prevent="updatePage(pages.currentPage - 1)"
        >
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li
        class="page-item"
        v-for="(page, key) in pages.pageTotal"
        :key="key"
        :class="{ active: page === pages.currentPage }"
      >
        <a class="page-link" href="#" @click.prevent="updatePage(page)">{{
          page
        }}</a>
      </li>
      <li
        class="page-item"
        :class="{
          disabled:
            pages.currentPage === pages.pageTotal || pages.pageTotal === 0,
        }"
      >
        <a
          class="page-link"
          href="#"
          aria-label="Next"
          @click.prevent="updatePage(pages.currentPage + 1)"
        >
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>
<script>
// :pages="{頁碼資訊}"
// @emitPages="更新頁面事件"
export default {
  props: ['pages'],

  methods: {
    updatePage(page) {
      this.$emit('change-pages', page);
    },
  },
};
</script>
